@import '../../assets/css/theme';
.tip {
  font-weight: 400;
  color: $color-text-default;
  line-height: 20px;
  display: flex;
  margin-bottom: 10px;
  font-family: PingFangSC-Regular, PingFang SC;
  &_left {
    width: 20px;
    flex-shrink: 0;
  }
  &_right {
    flex: 1;
  }
}
.token {
  background: $color-info-bg;
  border-radius: 5px;
  border: 1px solid $color-text-default-1;
  padding: 10px;
  display: flex;
  align-items: center;
  margin-top: 30px;
  &_img {
    width: 53px;
    height: 53px;
    margin-right: 10px;
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 50%;
  }
  &_right {
    flex: 1;
  }
}
.symbol {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .copy {
    width: 28px;
    height: 28px;
    display: inline-block;
    border: 1px solid $color-text-default-1;
    border-radius: 50%;
    line-height: 28px;
  }
}
.address {
  color: $color-a;
}
