@import 'theme';

.mr-0 {
  margin-right: 0 !important;
}

html,
body {
  background: $color-bg;
  font-family: AvenirNext-Regular;
  font-size: 14px;
  color: $color-text-default;
  overscroll-behavior: none;

  .ib-parent {
    display: block;

    div,
    p,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      display: inline-block;
    }
  }

  .ant-modal-mask,
  .ant-modal-wrap {
    z-index: 1001;
  }

  /* 提示问号样式通用 */
  .ques {
    color: nth($black, 3);
    display: inline-block;
    background: #fff;
    min-width: 18px;
    width: 18px;
    height: 18px;
    line-height: 18px;
    text-align: center;
    border-radius: 100%;
    // margin-left: 10px;
    margin-right: 10px;
    border: 1px solid nth($black, 3);
    font-family: AvenirNext-Medium;
    font-size: 16px;
    cursor: pointer;
  }

  /* 链接通用 */
  .link {
    color: $blue;
    cursor: pointer;
    font-family: AvenirNext-Medium;
    font-size: 14px;

    &:hover {
      color: $hoverBlue;
    }
  }

  .bold {
    font-weight: 600;
  }

  .fz12 {
    font-size: 12px;
  }

  .fz13 {
    font-size: 13px;
  }

  .fz14 {
    font-size: 14px;
  }

  .fz16 {
    font-size: 16px;
  }

  .black {
    color: nth($black, 1);
  }

  /* 通用标题 */
  .firstTitle {
    font-family: AvenirNext-DemiBold;
    font-size: 28px;
    color: #333333;
  }

  .secondTitle {
    font-family: AvenirNext-Medium;
    font-size: 20px;
    color: #333333;
  }
}

.typo-text-link {
  font-family: AvenirNext-Medium;
}

.ant-notification-notice {
  border-radius: 30px;
}

.mobile-menu {
  display: none;
}

.justify-content {
  justify-content: space-between;
}

@media screen and (max-width: 1000px) {
  .mobile-menu {
    display: block;
    width: 60px;
    height: 22px;
    color: #fff;
    position: absolute;
    top: 5px;
    left: 10px;
    cursor: pointer;
  }

  .swap-content .swap-block .left-block,
  #swap-tab .action-content .token-sec .input {
    flex: 1;

    input {
      width: 100%;
    }
  }

  .swap-content .swap-block .right-block,
  #swap-tab .action-content .token-sec .select {
    width: 55%;

    .input-max {
      display: inline-block;

      .link {
        vertical-align: top;
        margin-top: 4px;
        display: inline-block;
        margin-right: 5px;
      }
    }

    p:first-child {
      width: 100%;
    }

    .dragDown,
    p:first-child {
      display: inline-block;
      line-height: 30px;
      margin-top: 0;

      .balance {
        max-width: 150px;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }

  .ant-modal.pool-modal.add-gai .amount .amount-num {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 100%;
  }

  .swap-content .swap-block .right-block {
    .link {
      margin-right: 10px !important;
      padding: 0 7px !important;
    }

    .dragDown {
      padding-right: 10px;
      margin-top: 10px !important;
    }
  }

  body #root .action-content .action-info .schedule-key {
    display: flex;
    justify-content: space-between;
  }

  .base-home-swapmodal .ant-modal-close,
  .base-home-swapmodal .ant-modal-close {
    top: 30px !important;
    right: 30px !important;
  }

  .account-modal .address-con .address-tex span:first-child {
    word-break: break-all;
    max-width: 80%;
  }

  .ant-modal.pool-modal .price .param.flex:first-child {
    .price-value {
      width: 70%;
      text-align: right;
    }
  }

  .ant-modal.pool-modal .price .param.flex:nth-child(2) {
    display: flex;

    .price-value {
      width: 70%;
      text-align: right;
    }
  }

  .ant-modal.pool-modal .amount .symbol {
    margin: 0;
  }

  .ant-modal.pool-modal .amount .amount-num {
    max-width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .action-content .convert {
    align-items: center;
  }

  .ant-modal.login-modal {
    // transform-origin: inherit;
    .logo {
      margin-top: 20px;
    }

    .wallet-list {
      margin-top: 20px;
    }
  }

  .ant-drawer-header-no-title .ant-drawer-close {
    padding: 0;
    top: 35px;
    right: 30px;
  }

  .m-menu-drawer .ant-drawer-header-no-title .ant-drawer-close {
    padding: 0;
  }

  .ant-modal-wrap .ant-modal.select-modal {
    .select-search {
      background-position: 105% 30% !important;
      background-size: 80px;
    }

    .ant-modal-close {
      top: 30px;
    }
  }

  .ant-notification-notice {
    width: 300px;
  }

  .ant-notification.ant-notification-topRight {
    margin-right: 0;
    margin-left: 50%;
    transform: translateX(-50%);
    right: auto !important;
    width: 100%;

    .ant-notification-notice {
      width: 100%;
    }
  }
}

.ant-table-content {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.flex {
  display: flex;
}

.ant-drawer .activity.gift-activity img {
  width: 220px;
  height: auto;
}

.ant-zoom-sun-enter,
.ant-zoom-sun-appear {
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}
.ant-zoom-sun-leave {
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}
.ant-zoom-sun-enter.ant-zoom-sun-enter-active,
.ant-zoom-sun-appear.ant-zoom-sun-appear-active {
  -webkit-animation-name: antZoomInSun;
  animation-name: antZoomInSun;
  -webkit-animation-play-state: running;
  animation-play-state: running;
}
.ant-zoom-sun-leave.ant-zoom-sun-leave-active {
  -webkit-animation-name: antZoomOutSun;
  animation-name: antZoomOutSun;
  -webkit-animation-play-state: running;
  animation-play-state: running;
  pointer-events: none;
}
.ant-zoom-sun-enter,
.ant-zoom-sun-appear {
  transform: scale(0);
  opacity: 0;
  -webkit-animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
  animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
}
.ant-zoom-sun-enter-prepare,
.ant-zoom-sun-appear-prepare {
  transform: none;
}
.ant-zoom-sun-leave {
  -webkit-animation-timing-function: cubic-bezier(0.78, 0.14, 0.15, 0.86);
  animation-timing-function: cubic-bezier(0.78, 0.14, 0.15, 0.86);
}

@keyframes antZoomInSun {
  0% {
    transform: scale(0.2);
    opacity: 0;
  }

  100% {
    transform: scale(0.8);
    opacity: 1;
  }
}

@keyframes antZoomOutSun {
  0% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(0.2);
    opacity: 0;
  }
}

@media screen and (max-width: 1000px) {
  .scan-home {
    .toast {
      margin-top: -20px;
    }
  }

  .scan-home .chart-area .chart-liquidity .chart-stats-data {
    margin-top: -3px !important;

    &.mobile-mt {
      margin-top: 7px !important;
    }
  }

  // modal
  .base-home-swapmodal .ant-modal,
  .base-home-swapmodal .ant-modal {
    width: 100% !important;
    top: calc(50vh - 270px);

    &.list-remove-modal {
      margin-left: 0 !important;
      transform-origin: center !important;
    }

    &.list-addtoken-modal {
      margin-left: 0 !important;
    }
  }

  .base-home-swapmodal .ant-modal-content,
  .base-home-swapmodal .ant-modal-content,
  .base-home-swapmodal .ant-modal.pool-modal .ant-modal-content,
  .base-home-swapmodal .ant-modal.list-modal .ant-modal-content {
    padding: 20px !important;
    border-radius: 20px;
  }

  .base-home-swapmodal .ant-notification-notice.swap-noti {
    top: 30px;
  }
}
