@import './var.scss';
@import './theme';
@import './mixins';

.tr-tooltip {
  .ant-tooltip-inner {
    width: fit-content;
    padding: 0;
    border-radius: 20px;
    overflow: hidden;
    .tr-content li {
      width: 450px;
    }
  }
}

.trade-router-modal.ant-modal {
  .ant-modal-close {
    display: none;
  }
  .ant-modal-content {
    background: $color-input-bg;
    padding: 0;
    border-radius: 20px;
    overflow: hidden;
  }

  .ant-modal-title {
    text-align: left !important;
    font-size: 15px !important;
  }
  .ant-modal-close {
    display: none;
  }
}

.trade-router {
  font-size: 14px;
  color: $color-text-default;
  line-height: 20px;
  cursor: pointer;
  @include mobile-only {
    text-align: right;
  }
  .renderRoutrOuter {
    display: flex;
    .renderRoutrOuter-r {
      flex: 1;
    }
  }
  .trade-router-txt {
    margin-right: 10px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    opacity: 0.5;
    white-space: nowrap;
  }
  .tr-arrow {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: url(../images/homeTokens/ico_router_path.png) 0 0 no-repeat;
    background-size: 100% 100%;
    margin: 0 8px;
    @include lightThemeOnly {
      background-image: url(../images/homeTokens/ico_router_path_light.png);
    }
  }

  img {
    position: relative;
    width: 15px;
    margin-right: 6px;
    vertical-align: middle;
    top: -1px;
    border-radius: 50%;
    border: 1px solid #ddd;
    background: #fff;

    @include mobile-only {
      width: 16px;
    }
  }
}

.tr-content {
  color: #ffffff;
  .tr-content-title {
    font-size: 20px;
    font-weight: 600;
    color: #2c3039;
  }
  .token {
    margin-right: 4px;
  }

  > p {
    display: flex;
    align-items: center;
    font-family: Avenir Next;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
    margin-bottom: 16px;

    > img {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }
  }

  > div {
    min-width: 160px;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  li {
    list-style: none;
    padding: 16px 16px 0 16px;
    color: $color-text-default;
    position: relative;
    // background: $color-info-card-bg;
    background: $color-input-bg;
    border-bottom: 1px solid $color-border-primary;
    &:last-child {
      border-bottom: 0;
    }
    .tr-arrow {
      display: block;
      width: 8px;
      height: 8px;
      background: url(../images/homeTokens/arrow_default.png) 0 0 no-repeat;
      background-size: 100% 100%;
    }
    &.active {
      background: $color-info-card-bg;
      &::after {
        content: '';
        border: none;
        background: url(../images/homeTokens/router-path-checked.png) no-repeat;
        background-size: 100% 100%;
      }
      .tr-content-li-title {
        @include lightThemeOnly {
          color: rgba(114, 47, 255, 1);
        }
      }
      .tr-content-li-top-r {
        @include lightThemeOnly {
          color: rgba(114, 47, 255, 0.5);
        }
      }
    }
    &::after {
      content: '';
      position: absolute;
      top: 16px;
      right: 16px;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      border: solid 1px $color-text-default;
    }
    &:hover {
      // background: #4d4366;
      background: $color-info-card-bg;
    }
    &:last-child {
      .tr-content-li-down {
        border-bottom: none;
      }
    }

    .tr-content-li-top {
      display: flex;
      padding-right: 22px;
      margin-bottom: 10px;
      > span {
        font-size: 15px;
        font-weight: 400;
        opacity: 0.5;
      }
      .tr-content-li-title {
        font-size: 15px;
        font-weight: 600;
        margin-right: 10px;
        opacity: 1;
        white-space: nowrap;
      }
      .tr-content-li-top-r {
        flex: 1;
        width: 0;
        color: $color-text-default-7;
        word-break: break-all;
        font-family: PingFangSC-Regular, PingFang SC, sans-serif;
      }
    }
    .tr-content-li-top-r-num {
      margin: 0 4px;
    }
    .tr-content-li-down {
      display: flex;
      flex-flow: wrap;
      font-size: 15px;
      font-weight: 400;
      //border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      padding-bottom: 6px;

      .tr-content-li-pair {
        display: flex;
        align-items: center;
        margin-right: 8px;
        margin-bottom: 10px;
        .tr-content-li-pair-op {
          display: flex;
          align-items: center;
          font-size: 14px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: $color-text-default;
        }
        img {
          width: 18px;
          height: 18px;
          margin-right: 4px;
          border-radius: 50%;
          border: 1px solid #ddd;
          background: #fff;
        }
        &:last-child {
          margin-right: 0;

          .tr-arrow {
            display: none;
          }
        }
        .path-pool {
          align-items: center;
        }
        .tr-pool {
          margin: 0 8px;
          background: $color-border-primary;
          color: $color-text-default-5;
          padding: 0 4px;
          border-radius: 2px;
          font-size: 11px;
          line-height: 18px;
        }
      }
    }
  }
}
