@import '../../../assets/css/theme';

.setting_popover.ant-popover {
  width: 272px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  .ant-popover-inner {
    background: $color-info-bg;
  }
  .ant-popover-arrow {
    opacity: 0;
  }
}
#root .new_setting_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border: 1px solid $color-border-primary;
  color: $color-text-default;
  border-radius: 50%;
  text-align: center;
  // display: inline-block;
  line-height: 28px;
  cursor: pointer;
  &:hover {
    border: 1px solid $blue;
    background-color: $blue;
    color: #fff;
    .new_setting_icon_dark {
      display: block !important;
    }
    .new_setting_icon_light {
      display: none !important;
    }
  }
  img {
    width: 16px;
    height: 16px;
  }
}
